.comics-header {
  margin-top: 50px;
  position: relative;
  box-sizing: border-box;
  width: 77vw;
  height: 12vh;
  background-color: black;
  color: white;
  display: flex;
  padding: 0vh 5vw;
  justify-content: space-between;
  font-size: 20px;
  align-items: center;
}
.comics-header img:nth-child(1) {
  object-fit: contain;
  height: 90%;
  /* width: 50%; */
}
.comics-header span {
  display: block;
}
.comics-content {
  width: 77vw;
  /* height: 80vh; */
  /* background-color: blue; */
  display: flex;
  flex-direction: column;
  align-items: center;
}
.comics-list {
  margin-top: 5vh;
  width: 77vw;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 5vh 2vw;
}
.comics-item {
  cursor: pointer;
  box-sizing: border-box;
  font-weight: 700;
  font-family: "Roboto Condensed";
  position: relative;
  height: 40vh;
  width: 16vw;
  display: flex;
  flex-direction: column;
  justify-content: start;
  padding: 0vh 0.5vw;

  /* box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2); */
}
.comics-item img:hover {
  transform: translateY(-5px);
}
.comics-item img {
  transition: transform 0.5s ease-in-out;
  width: 100%;
  height: 80%;
  object-fit: contain;
}
.comics-title {
  margin-top: 5px;
}
.comics-price {
  margin-top: 5px;
  color: rgba(0, 0, 0, 0.5);
}
.comics-content button {
  margin-top: 2vh;
}
